import React from 'react'
import Link from 'gatsby-link'

const NotFoundPage = () => (
  <div id="notfound">
    <h1>OH NO! You seem to be lost.</h1>
    <Link to="/">Click here to follow the trail of garlicoin back home.</Link>
  </div>
)

export default NotFoundPage



